import 'ol-layerswitcher/dist/ol-layerswitcher.css';
import Map from 'ol/Map';
import proj4 from 'proj4';
import { register } from 'ol/proj/proj4.js';
import OSM from 'ol/source/OSM';
import TileLayer from 'ol/layer/Tile';
import View from 'ol/View';
import WMTS from 'ol/source/WMTS';
import VectorLayer from 'ol/layer/Vector';
import VectorSource from 'ol/source/Vector';
import GeoJSON from 'ol/format/GeoJSON';
import { get as getProjection } from 'ol/proj';
import { BaseLayers as BaseLayersList } from './base_layers';
import LayerGroup from 'ol/layer/Group';
import MousePosition from 'ol/control/MousePosition';
import { createStringXY } from 'ol/coordinate';
import { METERS_PER_UNIT } from 'ol/proj/Units';
import LayerSwitcher from 'ol-layerswitcher';
import { fetchLayer } from './fetch_wmts_layer';
import { styleFunction } from './styling';

var olMap;

window.Omap =  {
  initMap: function() {


//Enverinment vars
var wmsHost = 'avalon.geo-volodarsk.com';
var wmsServer = 'http://' + wmsHost + ':8080';
var workSpace = 'egrn';
var workSpaceService = wmsServer + '/geoserver/' + workSpace;
var getCapabilities = workSpaceService + '/gwc/service/wmts?REQUEST=GetCapabilities&format=text/xml';

var bounds = [2130423.51, 512052.59, 2177478.69, 558388.62];

var projectionName = 'EPSG:905202';
proj4.defs(projectionName, '+title= -52  2   +proj=tmerc +lat_0=0 +lon_0=44.55 +k=1 +x_0=2250000 +y_0=-5714743.504 +ellps=krass +towgs84=23.57,-140.95,-79.8,0,0.35,0.79,-0.22 +units=m +no_defs');
register(proj4);
var targetProjection = getProjection(projectionName);
targetProjection.setExtent(bounds);




//#region Mouse position
var pointPosition = new MousePosition({
  className: 'custom-mouse-position',
  target: document.getElementById('currentPosition'),
  coordinateFormat: createStringXY(5),
  undefinedHTML: '&nbsp;'
});
//#endregion
//#region Layer switcher
const layerSwitcher = new LayerSwitcher({
  reverse: true,
  groupSelectStyle: 'group'
});
//#endregion

var BaseLayers = new LayerGroup({ title: ' ' });
BaseLayersList.forEach(layer => {
  var wmtsOptions = fetchLayer(layer.layerName, layer.style, getCapabilities);

  if (wmtsOptions != null) {
    var tileLayer = new TileLayer({
      opacity: 1,
      title: layer.title,
      source: new WMTS(wmtsOptions)
    });
    BaseLayers.getLayers().push(tileLayer);
  }
});



const map = new Map({
  layers: [
    new TileLayer({
      source: new OSM(),
      title: 'OpenStreetMap'
    })
  ],
  controls: [layerSwitcher, pointPosition],
  target: 'map',
  view: new View({
    center: [2153406.72, 531518.95],
    zoom: 3,
    projection: projectionName
  }),
});

olMap = map;
map.addLayer(BaseLayers);


//#region Scale Textbar
map.getView().on('change:resolution', function (evt) {
  var resolution = evt.target.get('resolution');
  var units = map.getView().getProjection().getUnits();
  var dpi = 25.4 / 0.28;
  var mpu = METERS_PER_UNIT[units];
  var scale = resolution * mpu * 39.37 * dpi;
  if (scale >= 9500 && scale <= 950000) {
    scale = Math.round(scale / 1000) + "K";
  } else if (scale >= 950000) {
    scale = Math.round(scale / 1000000) + "M";
  } else {
    scale = Math.round(scale);
  }
  document.getElementById('currentScale').innerHTML = " = 1 : " + scale;
});
//#endregion
},

addLayer: function(featureCollection, layerTitle) {
  var data = JSON.parse(featureCollection);
  Omap.loadGeoJsonLayer(data, layerTitle);
},

addLayers: function(featureCollections) {
  var data = JSON.parse(featureCollections);
  data.forEach(titledFeature => Omap.loadGeoJsonLayer(titledFeature.Item1, titledFeature.Item2));
},

//Load single vector layer from featureCollection
loadGeoJsonLayer: function(featureCollection, layertitle) {
  var _layertitle = layertitle == null ? '' : layertitle;
  const vectorSource = new VectorSource({
    features: new GeoJSON().readFeatures(featureCollection)
  });
  var vectLayer = new VectorLayer({ source: vectorSource, title: _layertitle, style: styleFunction });
  olMap.addLayer(vectLayer);
  Omap.zoomTofeature(vectorSource.getExtent());
  return true;
},
zoomTofeature: function (extent) {
  olMap.getView().fit(extent, olMap.getSize(), null);
}
}




