import {Circle as CircleStyle, Fill, Stroke, Style, Text} from 'ol/style';

export { styleFunction };
const styleFunction = function (feature) {
    var style = new Style({
      stroke: new Stroke({
        color: '#008000',
        width: 2
      }),
      fill: new Fill({
        color: 'rgba(127,204,25, 0.5)'//'rgba(0, 0, 255, 0.1)'
      }),
      image: new CircleStyle({
        radius: 3,
        fill: new Fill({
          color: '#7fcc19'
        }),
        stroke: new Stroke({
          color: '#008000',
          width: 1
        })
      }),
      text: createTextStyle(feature)
    });
    return [style];
  };
  
  const createTextStyle = function (feature) {
    return new Text({
      textAlign: 'center',
      textBaseline: 'middle',
      font: 'bold 12px/1 Verdana',
      text: feature.get('label'),
      fill: new Fill({ color: 'rgb(0,0,0)' }),
      stroke: new Stroke({ color: 'rgb(255,255,255)', width: 2 }),
      offsetX: 10,
      offsetY: 10,
      placement: 'point',
      maxAngle: 0.7853981633974483,
      overflow: 'false',
      rotation: 0,
    });
  };